<template>
    <div class="my-qcode">
        <div class="content">
            <div class="content-wapper">
              <img :src="this.icon" class="head-img">
              <p>{{this.username}}</p>
              <div id="my-qcode" class="canvas-container"></div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import QRCode from "qrcodejs2";

export default {
  data() {
    return {
      codes: "",
      intervalId: -1000,
      qrcode: null
    };
  },
  methods: {
    useqrcode() {
      let rem = parseFloat(
        window.getComputedStyle(document.documentElement)["fontSize"]
      );
      if (!this.qrcode)
        this.qrcode = new QRCode("my-qcode", {
          width: 1.7 * rem, // 设置宽度
          height: 1.7 * rem // 设置高度
        });
      let codeStr = "fuc:bind," + "userid:" + this.userid;
      if (this.role.toString() == '1') {
        codeStr =
          this.$store.state.domainUrl +
          "wxapi/GetUserInfo/dropindex.php?type=amlzlogin&userid=" +
          this.userid;
      } else if (this.role.toString() == '2') {
        codeStr =
          this.$store.state.domainUrl +
          "wxapi/GetUserInfo/dropindex.php?type=amlzlogin";
      }
      this.$nextTick(() => {
        this.qrcode.makeCode(codeStr);
      });
    },
    intervalHandle() {
      this.requestHandle();
    },
    requestHandle() {
      this.$http
        .post(this.path + "php/suncode/dealfuc.php", {
          fuc: "usecouponstate",
          hand: "use:deposit:" + this.userid
        })
        .then(response => {
          let data = response.body;
          if (data.toString() == "200") {
            this.scanSuccessHandle();
          }
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    scanSuccessHandle() {
      this.$store.commit("UPDATE_RECEIVEOKSHOW", true);
      this.updataData();
    },
    updataData() {
      this.$http
        .post(this.path + `php/ttl/userFunction.php`, {
          type: "userinfo",
          openid: this.openid
        })
        .then(response => {
          let data = response.body;
          this.$store.commit("UPDATE_ACCOUNTS", data.accounts);
          this.$store.commit("UPDATE_CLIENTNUM", data.clientNum);
          this.$store.commit("UPDATE_GIFTNUM", data.giftnum);
          this.$store.commit("UPDATE_EXPNUM", data.expnum);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    }
  },
  activated() {
    if (this.role.toString() == "3"&&this.intervalId!=-1000) {
      this.intervalId = setInterval(this.intervalHandle, 4000);
    }
    this.useqrcode();
  },
  mounted() {
  
  },
  computed: {
    ...mapState([
      "path",
      "userid",
      "role",
      "icon",
      "gender",
      "username",
      "openid"
    ])
  },
  components: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.my-qcode {
  background-color: #515151;
  color: #a2733d;
  position relative;
  
  .content {
    position absolute;
    left .25rem;
    right .25rem;
    top .45rem;
    bottom .45rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius 0.04rem;

    .content-wapper {
      text-align: center;

      .head-img {
        margin-top: 0.1rem;
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 0.8rem;
        border: 2px #a2733d solid;
        box-shadow: 0 0px 4px rgba(0, 0, 0, 0.6);
      }

      p {
        margin-top: 0.04rem;
        font-size: 0.13rem;
        letter-spacing .01rem;
      }

      .canvas-container {
        display: inline-block;
        margin-top: 0.36rem;
      }
    }
  }
}
</style>



